<template>
  <swiper class="swiper" :options="swiperOption">
    <swiper-slide class="text">
      <slot></slot>
      <div v-for="i in 200">{{i}}</div>
    </swiper-slide>
    <div class="swiper-scrollbar" slot="scrollbar"></div>
  </swiper>
</template>

<script>
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
  import 'swiper/css/swiper.css'
  export default {
    name: 'test',
    title: 'Scroll container',
    components: {
      Swiper,
      SwiperSlide
    },
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          slidesPerView: 'auto',
          freeMode: true,
          scrollbar: {
            el: '.swiper-scrollbar'
          },
          mousewheel: true
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .swiper {
    height: 100vh;
    /deep/ .swiper-slide {
      &.text {
        font-size: 18px!important;
        text-align: left!important;
        height: auto;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        padding: 30px;
        > .content {
          > .title {
            margin: 0;
            text-align: center;
            margin-bottom: .75em;
          }
          > p, blockquote {
            line-height: 2;
            text-indent: 2em;
          }
          blockquote {
            padding: 1rem;
            margin-left: 2em;
            background-color: #ddd;
          }
        }
      }
    }
  }
</style>


